3.8vw, 1.5rem);\n  --fontSize-large: clamp(1.2rem, 4.2vw, 2rem);\n  --fontSize-xlarge: clamp(4rem, 6vw, 4rem);\n\n  --tempGreen: #68c94f;\n  --tempLime: #b2d339;\n  --tempYellow: #efc336;\n  --tempOrange: #fdb13b;\n  --tempRedOrange: #f17627;\n  --tempRed: #e74515;\n  --white: #fff;\n}\n\n.theme-day {\n  --accentColor: #97edf1;\n  --bgColor: #50aad5;\n  --bgGradient: linear-gradient(to bottom, #368ab3, #50aad5);\n  --borderColor: #61b6df;\n  --buttonBgColor: #56c3f7;\n  --buttonBorderColor: #56c3f7;\n  --buttonIconColor: #fff;\n  --iconColor: #fff;\n  --infoBarColor: #2a7ea7;\n  --inputBorderColor: #dfdfdf;\n  --inputErrorColor: #f4cfcf;\n  --moduleBgColor: #61b6df;\n  --scrollBarThumbColor: #368ab3;\n  --scrollBarThumbColorHover: #368ab3;\n  --scrollBarTrackColor: transparent;\n  --scrollBarTrackColorHover: transparent;\n  --switchBgColor: #287093;\n  --switchColor: #56c3f7;\n  --switchTextColor: #fff;\n  --textColor: #fff;\n}\n\n.theme-night {\n  --accentColor: #52ace3;\n  --bgColor: #347399;\n  --bgGradient: linear-gradient(to bottom, #2c6688, #347399);\n  --borderColor: #4286af;\n  --buttonBgColor: #4ca6dd;\n  --buttonBorderColor: #65b8eb;\n  --buttonIconColor: #fff;\n  --iconColor: #fff;\n  --infoBarColor: #225574;\n  --inputBorderColor: #dfdfdf;\n  --inputErrorColor: #f4cfcf;\n  --moduleBgColor: #4286af;\n  --scrollBarThumbColor: #276387;\n  --scrollBarThumbColorHover: #276387;\n  --scrollBarTrackColor: transparent;\n  --scrollBarTrackColorHover: transparent;\n  --switchBgColor: #1d4e6c;\n  --switchColor: #4ca6dd;\n  --switchTextColor: #fff;\n  --textColor: #fff;\n}\n\n.theme-default {\n  --accentColor: #f33737;\n  --bgColor: #fff;\n  --bgGradient: linear-gradient(to bottom, #28627e, #396981);\n  --borderColor: #dfdfdf;\n  --buttonBgColor: #dfdfdf;\n  --buttonIconColor: #fff;\n  --iconColor: #000;\n  --infoBarColor: #e7f3f1;\n  --inputBorderColor: #dfdfdf;\n  --inputErrorColor: #fff2f2;\n  --moduleBgColor: #f7f7f7;\n  --scrollBarThumbColor: orange;\n  --scrollBarThumbColorHover: pink;\n  --scrollBarTrackColor: transparent;\n  --scrollBarTrackColorHover: transparent;\n  --switchBgColor: #dfdfdf;\n  --switchColor: #aaa;\n  --switchTextColor: #dfdfdf;\n  --textColor: #000;\n}\n\n/* Scrollbar */\n::-webkit-scrollbar {\n  height: 5px;\n  width: 5px;\n}\n\n::-webkit-scrollbar-thumb {\n  background: var(--scrollBarThumbColor);\n}\n\n::-webkit-scrollbar-thumb:hover {\n  background: var(--scrollBarThumbColorHover);\n}\n\n::-webkit-scrollbar-track {\n  background: var(--scrollBarTrackColor);\n}\n\n::-webkit-scrollbar-track:hover {\n  background: var(--scrollBarTrackColorHover);\n}\n\n::selection {\n  background-color: var(--accentColor);\n  color: #fff;\n}\n\n* {\n  box-sizing: border-box;\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\nhtml, body {\n  background-color: var(--bgColor);\n  background-image: var(--bgGradient);\n  color: var(--textColor);\n  font-family: var(--fontStack);\n  font-size: var(--fontSize-medium);\n  line-height: 1.61;\n}\n\na {\n  color: var(--accentColor);\n}\n\nh1 {\n  font-size: var(--fontSize-large);\n}\n\nbutton {\n  appearance: none;\n  border: 0;\n  cursor: pointer;\n}\n\ninput, textarea, select, option {\n  display: block;\n  font-family: var(--fontStack);\n  padding: .4rem 1rem;\n  width: 100%;\n\n  &:focus {\n    outline: none;\n  }\n}\n\nheader, form, form button, #info-bar, #info-bar .left, #info-bar .right, main, .today, .highs-lows, .hourly, .hour, .days-wrapper, .day, .air-quality-wrapper, .air-quality-wrapper > div, .us-epa-index, main .title, .index-score {\n  display: grid;\n}\n\n#container {\n  background-image: var(--bgGradient);\n  display: none;\n  margin: 0 auto;\n  max-width: 1000px;\n  padding: 1rem;\n\n  header {\n    align-items: center;\n    gap: .5rem;\n    grid-template-columns: auto 1fr;\n    min-height: 2.7rem;\n\n    form {\n      gap: .5rem;\n      grid-template-columns: 1fr auto;\n      height: min-content;\n\n      input {\n        border: 1px solid var(--inputBorderColor);\n        border-radius: var(--borderRadius);\n        min-width: 200px;\n\n        &.error {\n          background-color: var(--inputErrorColor);\n        }\n      }\n\n      button {\n        align-items: center;\n        background-color: var(--buttonBgColor);\n        border: 1px solid var(--buttonBorderColor);\n        border-radius: var(--borderRadius);\n        font-size: var(--fontSize-small);\n        justify-content: center;\n        padding: 0 .5rem;\n        transition: transform;\n\n        svg {\n          fill: var(--buttonIconColor);\n        }\n\n        &:active {\n          transform: translateY(1px);\n        }\n      }\n    }\n\n    #info-bar {\n      background-color: var(--infoBarColor);\n      border-radius: var(--borderRadius);\n      font-size: var(--fontSize-small);\n      grid-column: span 2;\n      grid-template-columns: auto 1fr;\n      height: 32px;\n  \n      .loading {\n        padding-left: 1.8rem;\n      }\n  \n      .left {\n        align-items: center;\n        gap: .2rem;\n        grid-template-columns: 20px auto;\n        padding-left: .2rem;\n  \n        .location {\n          width: max-content;\n        }\n      }\n  \n      .right {\n        align-items: center;\n        gap: .5rem;\n        grid-template-columns: 20px auto;\n        justify-content: end;\n        padding-right: .5rem;\n      }\n    }\n  }\n\n  main {\n    column-gap: 2rem;\n    grid-template-columns: repeat(6, 1fr);\n    margin: 2rem 0;\n\n    .loading-icon {\n      grid-column: span 6;\n      margin: 0 auto;\n\n      svg {\n        g {\n          rect {\n            fill: var(--infoBarColor);\n          }\n        }\n      }\n    }\n\n    .title {\n      align-items: center;\n      border-bottom: 1px solid var(--borderColor);\n      gap: .3rem;\n      grid-template-columns: auto 1fr;\n      margin-bottom: 1.5rem;\n      padding-bottom: .5rem;\n\n      svg {\n        fill: var(--iconColor);\n      }\n    }\n\n    .today {\n      justify-content: center;\n      grid-column: 1 / -1;\n      margin-bottom: 2rem;\n      text-align: center;\n\n      h2 {\n        font-size: var(--fontSize-mlarge);\n        line-height: 1;\n      }\n\n      .temperature {\n        font-size: var(--fontSize-xlarge);\n        font-weight: bold;\n        line-height: 1;\n        margin-bottom: .8rem;\n      }\n\n      .condition {\n        font-size: var(--fontSize-large);\n        margin-bottom: .5rem;\n      }\n\n      .highs-lows {\n        gap: 1rem;\n        grid-template-columns: auto auto;\n        justify-content: center;\n        margin-bottom: .2rem;\n        \n        .temperature {\n          font-size: var(--fontSize-medium);\n        }\n      }\n\n      .icon {\n        justify-self: center;\n      }\n    }\n\n    .hourly {\n      gap: 1rem;\n      grid-column: 1 / -1;\n      grid-template-columns: repeat(23, 1fr);\n      margin-bottom: 2rem;\n      overflow-x: auto;\n      overflow-y: hidden;\n      padding-bottom: 1rem;\n\n      .hour {\n        background-color: var(--moduleBgColor);\n        border-radius: var(--borderRadius);\n        padding: 1rem;\n        text-align: center;\n      }\n    }\n\n    .five-day {\n      grid-column: span 3;\n\n      .days-wrapper {\n        gap: 2rem;\n\n        .day {\n          align-items: center;\n          gap: 1rem;\n          grid-template-columns: max-content auto  max-content 1fr max-content;\n\n          .dayOfWeek {\n            width: 2rem;\n          }\n\n          .min-temp, .max-temp {\n            width: 3.5rem;\n          }\n\n          .temperature-bar {\n            background: linear-gradient(to right, var(--tempGreen), var(--tempOrange), var(--tempRed));\n            border-radius: var(--borderRadius);\n            height: 5px;\n            width: 100%; \n          }\n        }\n      }\n    }\n\n    .air-quality {\n      grid-column: span 3;\n      \n      .air-quality-wrapper {\n        gap: .5rem;\n        grid-template-columns: repeat(3, 1fr);\n\n        >div {\n          align-items: center;\n          background: var(--moduleBgColor);\n          border-radius: var(--borderRadius);\n          font-size: var(--fontSize-mlarge);\n          gap: .5rem;\n          justify-items: center;\n          padding: 1rem;\n  \n          .text {\n            font-size: var(--fontSize-xsmall);\n            line-height: 1.3;\n            max-width: 9ch;\n            text-align: center;\n          }\n        }\n      }\n\n      .us-epa-index {\n        background-color: transparent !important;\n        gap: 0 !important;\n        grid-column: span 3;\n        grid-template-columns: 1fr 1fr;\n        padding-top: 0 !important;\n\n        .index-score {\n          align-content: center;\n          border-radius: 50%;\n          color: var(--white);\n          font-size: var(--fontSize-mlarge);\n          font-weight: bold;\n          grid-row: span 2;\n          height: 3rem;\n          justify-items: center;\n          justify-self: start;\n          width: 3rem;\n\n          &.index-1 {\n            background-color: var(--tempGreen);\n          }\n\n          &.index-2 {\n            background-color: var(--tempLime);\n          }\n\n          &.index-3 {\n            background-color: var(--tempYellow);\n          }\n\n          &.index-4 {\n            background-color: var(--tempOrange);\n          }\n\n          &.index-5 {\n            background-color: var(--tempRedOrange);\n          }\n\n          &.index-6 {\n            background-color: var(--tempRed);\n          }\n        }\n\n        .index-text {\n          justify-self: end;\n          text-align: right;\n        }\n\n        .index-subtext {\n          font-size: var(--fontSize-xsmall);\n          justify-self: end;\n          text-align: right;\n        }\n      }\n    }\n\n  }\n\n  footer {\n    text-align: center;\n\n    p {\n      font-size: var(--fontSize-xsmall);\n    }\n  }\n}\n\n/* Temp toggle */\n.switch {\n  display: inline-block;\n  height: 28px;\n  justify-self: end;\n  position: relative;\n  width: 56px;\n  \n  input {\n    opacity: 0;\n    height: 0;\n    width: 0;\n\n    &:checked + .slider {\n      background-color: var(--switchBgColor);\n    }\n    \n    &:focus + .slider {\n      box-shadow: 0 0 1px var(--switchBgColor);\n    }\n    \n    &:checked + .slider:before {\n      color: var(--switchTextColor);\n      content: 'F';\n      -webkit-transform: translateX(28px);\n      -ms-transform: translateX(28px);\n      transform: translateX(28px);\n    }\n  }\n\n  .slider {\n    bottom: 0;\n    background-color: var(--switchBgColor);\n    border-radius: var(--borderRadius);\n    cursor: pointer;\n    left: 0;\n    position: absolute;\n    right: 0;\n    top: 0;\n    transition: .2s;\n    -webkit-transition: .2s;\n\n    &:before {\n      align-items: center;\n      bottom: 0;\n      border-radius: var(--borderRadius);\n      background-color: var(--switchColor);\n      color: var(--switchTextColor);\n      content: 'C';\n      display: grid;\n      font-size: var(--fontSize-medium);\n      font-weight: bold;\n      height: 28px;\n      justify-content: center;\n      left: 0;\n      line-height: 28px;\n      position: absolute;\n      transition: .2s;\n      width: 28px;\n      -webkit-transition: .2s; \n    }\n  }\n}\n\n@media screen and (max-width: 700px) {\n  #container {\n    main {\n      .five-day {\n        .days-wrapper {\n          .day {\n            gap: 0;\n            grid-template-columns: repeat(3, 1fr);\n\n            .dayOfWeek {\n              grid-area: 1 / 1;\n            }\n\n            .icon-32 {\n              grid-area: 1 / 2;\n              justify-self: center;\n            }\n\n            .min-temp {\n              grid-area: 2 / 1;\n              width: auto;\n            }\n\n            .temperature-bar {\n              grid-area: 2 / 2;\n            }\n\n            .max-temp {\n              grid-area: 2 / 3;\n              justify-self: end;\n              width: auto;\n            }\n          }\n        }\n      }\n\n      .air-quality {\n        grid-template-columns: repeat(2, 1fr);\n\n        .air-quality-wrapper {\n          grid-template-columns: repeat(2, 1fr);\n\n          .us-epa-index {\n            grid-column: span 2;\n          }\n\n          >div {\n            padding: .5rem;\n          }\n        }\n      }\n    }\n  }\n}"],"sourceRoot":""}]);
