// Temperature
export function getCurrentTemp(responseData) {
  const { temp_c, temp_f } = responseData.current;
  let temp;

  if (localStorage.getItem('temp') === 'celsius') {
    temp = `${temp_c}C`;
  } else {
    temp = `${temp_f}F`;
  }

  return { temp };
}

function updateTemp() {
  const temps = document.querySelectorAll('.temperature');
  temps.forEach(temp => {

    // Slice off the unit and create a float
    const temperatureStr = temp.textContent.slice(0, -2);
    let temperature = parseFloat(temperatureStr);

    // Do the conversion and remove numbers with zeros after the decimal
    if (temp.textContent.includes('C')) {
      temperature = (temperature * 9/5) + 32;
      temperature = temperature.toFixed(1).replace(/\.0$/, '');
      temp.textContent = temperature + 'F';
    } else if (temp.textContent.includes('F')) {
      temperature = (temperature - 32) * 5/9;
      temperature = temperature.toFixed(1).replace(/\.0$/, '');
      temp.textContent = temperature + 'C';
    }
  });
}

const tempUnitToggle = document.querySelector('#switch-checkbox');
tempUnitToggle.addEventListener('change', () => {
  if (tempUnitToggle.checked) {
    localStorage.setItem('temp', 'fahrenheit');
    updateTemp();
  } else {
    localStorage.setItem('temp', 'celsius');
    updateTemp();
  }
});

// US EPA Index
export function getEPALevel(responseData) {
  let { 'us-epa-index': usEPAIndex } = responseData.current.air_quality;

  switch (responseData.current.air_quality['us-epa-index']) {
    case 1:
      usEPAIndex = 'Good';
      break;
    case 2:
      usEPAIndex = 'Moderate';
      break;
    case 3:
      usEPAIndex = 'Slightly unhealthy';
      break;
    case 4:
      usEPAIndex = 'Unhealthy';
      break;
    case 5:
      usEPAIndex = 'Very unhealthy';
      break;
    case 6:
      usEPAIndex = 'Hazardous';
      break;
  }

  return { usEPAIndex };
}