// 5-day Forecast
function show5Day(responseData) {
  const days = responseData.forecast.forecastday;
  const daysContainer = document.querySelector('main .five-day');
  
  if (daysContainer.innerHTML !== '') {
    daysContainer.innerHTML = '';
  }

  const usEPAIndexSVG = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  usEPAIndexSVG.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
  usEPAIndexSVG.setAttribute('viewBox', '0 -960 960 960');
  usEPAIndexSVG.setAttribute('fill', '#000');
  usEPAIndexSVG.setAttribute('width', '24');
  const usEPAIndexPath = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  usEPAIndexPath.setAttribute('d', 'M200-80q-33 0-56.5-23.5T120-160v-560q0-33 23.5-56.5T200-800h40v-80h80v80h320v-80h80v80h40q33 0 56.5 23.5T840-720v560q0 33-23.5 56.5T760-80H200Zm0-80h560v-400H200v400Zm0-480h560v-80H200v80Zm0 0v-80 80Zm280 240q-17 0-28.5-11.5T440-440q0-17 11.5-28.5T480-480q17 0 28.5 11.5T520-440q0 17-11.5 28.5T480-400Zm-160 0q-17 0-28.5-11.5T280-440q0-17 11.5-28.5T320-480q17 0 28.5 11.5T360-440q0 17-11.5 28.5T320-400Zm320 0q-17 0-28.5-11.5T600-440q0-17 11.5-28.5T640-480q17 0 28.5 11.5T680-440q0 17-11.5 28.5T640-400ZM480-240q-17 0-28.5-11.5T440-280q0-17 11.5-28.5T480-320q17 0 28.5 11.5T520-280q0 17-11.5 28.5T480-240Zm-160 0q-17 0-28.5-11.5T280-280q0-17 11.5-28.5T320-320q17 0 28.5 11.5T360-280q0 17-11.5 28.5T320-240Zm320 0q-17 0-28.5-11.5T600-280q0-17 11.5-28.5T640-320q17 0 28.5 11.5T680-280q0 17-11.5 28.5T640-240Z');
  usEPAIndexSVG.appendChild(usEPAIndexPath);

  const title = document.createElement('div');
  title.className = 'title';
  const textNode = document.createTextNode('5-day Forecast');
  title.append(usEPAIndexSVG);
  title.append(textNode);
  
  const daysWrapper = document.createElement('div');
  daysWrapper.className = 'days-wrapper';
  
  daysContainer.append(title, daysWrapper);

  days.forEach(day => {
    const dayDiv = document.createElement('span');
    dayDiv.className = 'dayOfWeek';
    const dateOrig = day.date;
    const newDate = format(new Date(dateOrig), 'EEE');
    dayDiv.append(newDate);

    const iconDiv = document.createElement('img');
    iconDiv.src = `https:${day.day.condition.icon}`;
    iconDiv.alt = day.day.condition.text;
    iconDiv.classList.add('icon-32', 'icon');
    iconDiv.width = 32;

    const tempBar = document.createElement('span');
    tempBar.className = 'temperature-bar';

    const minTempDiv = document.createElement('span');
    minTempDiv.classList.add('temperature', 'min-temp');
    const maxTempDiv = document.createElement('span');
    maxTempDiv.classList.add('temperature', 'max-temp');

    // Set the correct unit
    const { mintemp_c, mintemp_f, maxtemp_c, maxtemp_f } = day.day;
    let minTemp; let maxTemp;
  
    if (localStorage.getItem('temp') === 'celsius') {
      minTemp = `${mintemp_c}C`;
      maxTemp = `${maxtemp_c}C`;
    } else {
      minTemp = `${mintemp_f}F`;
      maxTemp = `${maxtemp_f}F`;
    }

    minTempDiv.textContent = minTemp;
    maxTempDiv.textContent = maxTemp;

    const daysDiv = document.createElement('div');
    daysDiv.className = 'day';
    daysDiv.append(dayDiv, iconDiv, minTempDiv, tempBar, maxTempDiv);
    daysWrapper.append(daysDiv);
  });

  const firstDayOfWeek = document.querySelector('.days-wrapper .day:first-child .dayOfWeek');
  firstDayOfWeek.textContent = 'Today';
}

// Hourly
function showHourly(responseData) {

  // Get local hour
  const [, localTime] = responseData.location.localtime.split(' ');
  let [currentLocalHour] = localTime.split(':'); // 0, 1, 2, 3, etc.

  // The API outputs a single digit for localTime, so we add a 0 for single digits to match the format of splitTime
  if (currentLocalHour >= 0 && currentLocalHour < 10) {
    currentLocalHour = `0${currentLocalHour}`; // 00, 01, 02, 03, etc.
  }

  // Today and tomorrow's hours
  const hours1 = responseData.forecast.forecastday[0].hour;
  const hours2 = responseData.forecast.forecastday[1].hour;

  // Filter out hours that have passed
  const filteredHours = hours1.filter(hour => {
    const [, splitTime] = hour.time.split(' ');
    const [splitHour] = splitTime.split(':'); // 00, 01, 02, 03, etc.

    return splitHour >= currentLocalHour;
  });

  // Join with next day's 'hour' array and limit output to 23
  const combinedHours = filteredHours.concat(hours2).slice(0, 23);

  const hoursContainer = document.querySelector('main .hourly');
  if (hoursContainer.innerHTML !== '') {
    hoursContainer.innerHTML = '';
  }

  combinedHours.forEach(hour => {
    const timeDiv = document.createElement('span');
    timeDiv.className = 'time';

    const [, splitTime] = hour.time.split(' ');
    let [splitHour] = splitTime.split(':')
    
    if (splitHour.toString().charAt(0) === '0') {
      splitHour = splitHour.slice(1); // 1, 2, 3, etc.
    }

    if (currentLocalHour.charAt(0) === '0') {
      currentLocalHour = currentLocalHour.slice(1);   
    }

    if (splitHour === currentLocalHour) {
      splitHour = 'Now';
    }

    if (splitHour >= 1 && splitHour < 12) {
      splitHour += 'am';
    }

    if (splitHour === '0') {
      splitHour = '12am';
    }

    if (splitHour === '12') {
      splitHour += 'pm';
    }

    if (splitHour > 12) {
      splitHour -= 12;
      splitHour += 'pm';
    }

    timeDiv.textContent = splitHour;

    const iconDiv = document.createElement('img');
    iconDiv.src = `https:${hour.condition.icon}`;
    iconDiv.alt = hour.condition.text;
    iconDiv.classList.add('icon-64', 'icon');

    const tempDiv = document.createElement('span');
    tempDiv.className = 'temperature';

    // Set the correct unit
    const { temp_c, temp_f } = hour;
    let temp;
  
    if (localStorage.getItem('temp') === 'celsius') {
      temp = `${temp_c}C`;
    } else {
      temp = `${temp_f}F`;
    }

    tempDiv.textContent = temp;

    const hoursDiv = document.createElement('div');
    hoursDiv.className = 'hour';
    hoursDiv.append(timeDiv, iconDiv, tempDiv);
    hoursContainer.append(hoursDiv);
  })

  hoursContainer.addEventListener('wheel', (e) => {
    e.preventDefault();

    const delta = Math.sign(e.deltaY);
    hoursContainer.scrollLeft += delta * 70;
  });
}

// Today
export function showToday(responseData) {
  console.log(responseData);

  // Get data
  const { name } = responseData.location;
  const { text, icon } = responseData.current.condition;
  const { temp } = getCurrentTemp(responseData);
  const { mintemp_c, maxtemp_c, mintemp_f, maxtemp_f } = responseData.forecast.forecastday[0].day;
  let { humidity } = responseData.current;
  const { sunrise } = responseData.forecast.forecastday[0].astro;
  const { sunset } = responseData.forecast.forecastday[0].astro;

  // Set the correct unit
  let minTemp; let maxTemp;

  if (localStorage.getItem('temp') === 'celsius') {
    minTemp = `${mintemp_c}C`;
    maxTemp = `${maxtemp_c}C`;
  } else {
    minTemp = `${mintemp_f}F`;
    maxTemp = `${maxtemp_f}F`;
  }

  // Today div
  const location = document.createElement('h2');
  location.append(name);

  const temperature = document.createElement('div');
  temperature.className = 'temperature';
  temperature.append(temp);

  const condition = document.createElement('div');
  condition.className = 'condition';
  condition.append(text);

  const highsAndLows = document.createElement('div');
  highsAndLows.className = 'highs-lows';
  const highs = document.createElement('span');
  highs.className = 'temperature';
  highs.textContent = maxTemp;
  const lows = document.createElement('span');
  lows.className = 'temperature';
  lows.textContent = minTemp;

  const humidityDiv = document.createElement('div');
  humidityDiv.className = 'humidity';
  humidity = `Humidity: ${humidity}%`
  humidityDiv.append(humidity);

  const textNode1 = document.createTextNode('H: ');
  const textNode2 = document.createTextNode('L: ');
  const highsDiv = document.createElement('div');
  highsDiv.className = 'highs';
  highsDiv.append(textNode1, highs);
  const lowsDiv = document.createElement('div');
  lowsDiv.className = 'lows';
  lowsDiv.append(textNode2, lows);
  highsAndLows.append(highsDiv, lowsDiv);

  const tempIcon = document.createElement('img');
  tempIcon.src = `https:${icon}`;
  tempIcon.alt = text;
  tempIcon.classList.add('icon-64', 'icon');

  const today = document.querySelector('.today');

  if (today.innerHTML !== '') {
    today.innerHTML = '';
  }

  today.append(location, tempIcon, temperature, condition, highsAndLows, humidityDiv);

  // Show other modules
  showHourly(responseData);
  show5Day(responseData);
  showAirQuality(responseData);
}